import {Link} from 'react-router-dom';
import React from 'react';
export default function ()
{const mySytle={
	height: "40px",
    width: "180px",
    fontSize: "20px",
    marginLeft: "20px",
    background: "linear-gradient(45deg, #006139,#6ff794,#6fd5f7,#7e53f5,#f23379)",
    border: "none",
    borderRadius: "20px",
    fontWeight: "700",
    cursor:"pointer",
    marginTop:"10px"
}
		return(
			<div>
			<Link to='/'><button style={mySytle}  >HOME</button></Link>
			<Link to='/about'><button style={mySytle}>About</button></Link>
			<Link to='/todo'><button style={mySytle}>TODO</button></Link>
			<Link to='/git'><button style={mySytle}>GIT</button></Link>
			<Link to='/login'><button style={mySytle}>LOGOUT</button></Link>
			</ div>
			);

	
}
