import React from 'react';
import Input from "./Components/input"
import Button from "./Components/button"

class App extends React.Component{

	state={
		a:"",
		todo:[]
	}
	

	handleInput=(e)=>
	{
		const value=e.target.value;
		this.setState({a:value,b:12})
	}
	setValue=()=>
	{
		const v=this.state.a;
		const todo=this.state.todo;
		todo.push(v);
		this.setState({todo:todo})
		console.log(this.state.todo)
	}
	delete=(e)=>
	{
		this.state.todo.splice(e.target.id,1);
		this.setState({todo:this.state.todo})
	}
	render()
	{
		// console.log(this.props.match.params.id);
		const{a:value}=this.state;
  return (
    <div className="App">
     
        <Input onChange={this.handleInput} placeholder="hello"/>
        <input value={value}

        onChange={this.handleInput}
        
         />
         <input type="button" value="submit" onClick={this.setValue}/>
         <ul>
         {
         this.state.todo.map((item,index)=>
         {
         	return(
         		<div>
         		<li key={index}>{item}</li>
         		<Button id={index} onClick={this.delete} value="Delete"/>
         		
         		</div>
         		);
         })
     }
    	</ul>
    </div>
  );

}
}

export default App;