import React from 'react';

class Login extends React.Component
{

	check()
	{
		var abc=[];
		var c={
			name:'sdc',
			pass:'asd'
		}
		abc.push(c);
		abc=JSON.stringify(abc);
		localStorage.setItem("username",abc)
		var namm=document.getElementById("username").value;
		var pass=document.getElementById("password").value;
		var local=localStorage.getItem("username");
		local=JSON.parse(local);
		

		for(var i=0;i<local.length;i++)
		{
			
			if(local[i].name==namm&&local[i].pass==pass)
			{
				console.log("yes");
				sessionStorage.setItem("user","1");
				window.location='/';
			}

		}
	}


	render()
	{
		if(sessionStorage.getItem("user"))
		{sessionStorage.clear();
			window.location='/';
		}

		return(
			<div>
			<input type="text" id="username"/>
			<input type="text" id="password"/>
			<button id="submit" onClick={this.check} > Submit </button>
			</div>
			)
	}
}

export default Login;