import React from 'react';
import {Link} from 'react-router-dom';
class Git extends React.Component
{
	state={
		users:[],
		id:String
	}

	componentDidMount()
	{
		var name=this.state.id;
		var xhr1=new XMLHttpRequest();
	xhr1.addEventListener('load',(req,res)=>
{
	
	var abc=JSON.parse(xhr1.responseText);
	console.log(abc);
	const users=this.state.users;
	users.push(abc);
	// this.state.users=abc;
	this.setState({users:users});
	
});
	xhr1.open("GET","https://api.github.com/users/"+name);
    xhr1.setRequestHeader("Content-Type","application/json");
     xhr1.send();
	}


	render()
	{
		this.state.id=this.props.match.params.id;
		console.log(this.props);
		const users=this.state.users;
		if(users.length>0)
		{
		

			return(
			<div id="main">
			{


				users.map((item)=>{
					
					return(
						<div>
						<img src={item.avatar_url}  height="100px"/>
						<p>{item.login}</p>
					   <p> FOLLOWERS : {item.followers}</p>
					   <p> FOLLOWING : {item.following}</p>
					   <p> REPOSITORY COUNT : {item.public_repos}</p>
					   <p> {item.company}</p>
					   <p> {item.blog}</p>

						</div>)
						
					
				})	
			}

			  </div>
			
				
			)
		}
		else
			return(
				<div>DATA IS COMING </div>
				)
	}
}

export default Git;