import React from 'react';
import {Link} from 'react-router-dom';
class Git extends React.Component
{
	state={
		users:[]

	}

	componentDidMount()
	{
		var xhr1=new XMLHttpRequest();
	xhr1.addEventListener('load',(req,res)=>
{
	
	var abc=JSON.parse(xhr1.responseText);
	console.log(abc);
	const users=this.state.users;
	for(var i=0;i<abc.length;i++)
	{
		users.push(abc[i].login);
	}
	// this.state.users=abc;
	this.setState({users:users});
	
});
	xhr1.open("GET","https://api.github.com/users");
    xhr1.setRequestHeader("Content-Type","application/json");
     xhr1.send();
	}
   
    getData()
    {
    	var url='/git/'+document.getElementById('in').value;
         window.location = url;
    }

	render()
	{

		console.log(this.props);
		const users=this.state.users;

		if(users.length>0)
		{
			// setTimeout(()=>
			// {
			// 	this.props.history.goBack();
			// },3000)

			return(

				
				
			<div id="main">
			<input type="text" id="in" /> 
			<button onClick={this.getData}>SUBMIT</button>
			{


				users.map((item)=>{
					const url='/git/'+item;
					return(<div>

						<Link to={url}>{item}</Link>
						
					     

						</div>)
					
				})	
			}

			  </div>
			
				
			)
		}
		else
			return(
				<div>DATA IS COMING </div>
				)
	}
}

export default Git;